<template>
    <transition name="indexScroll" appear>
        <div class="yuki-scroll-position selectNG" :class="[{'yuki-scroll-disappear':menuStatus || !scrollChange}]">
            <span id="index-scroll">scroll</span><br>
            <div id="yuki-arrow" style=""><span class="arrowUp"></span></div>
        </div>
    </transition>
</template>

<script>
    export default {
        name: "IndexScroll",
        props: ['menuStatus','scrollChange'],
    }
</script>

<style scoped>
    .yuki-scroll-disappear{
        opacity: 0;
    }
    .yuki-scroll-position {
        position: absolute;
        bottom: 5vh;
        width: 100vw;
        text-align: center;
        z-index: 10;
        transition: 0.5s ease-in-out;
    }

    #index-scroll {
        color: white;
        font-family: sans-serif;
        letter-spacing: 1px;
        font-size: 14px;
    }

    .arrowUp:after {
        content: '';
        display: inline-block;
        width: 8px;
        height: 8px;
        border-top: 1px solid #fff;
        border-right: 1px solid #fff;
        transform: rotate(135deg); /*旋转角度*/
        -webkit-transform: rotate(135deg);
    }

    #yuki-arrow {
        animation: yukiArrow 1.4s cubic-bezier(0.215, 0.61, 0.355, 1) 0s infinite alternate;
        margin-left: 0;
        margin-right: 0;
        margin-top: 6px;
    }

    @media only screen and ( max-width: 767px) {
        .yuki-scroll-position{
            display: none;
        }
    }

    .indexScroll-enter,
    .indexScroll-leave-to {
        opacity: 0;
    }

    .indexScroll-enter-active,
    .indexScroll-leave-active {
        transition: all 1.5s ease-in-out;
        transition-delay: 2.7s;

    }
</style>