import Vue from 'vue'
import './plugins/axios'
import App from './App.vue'
import router from './router'
import store from './store'
import './assets/css/yukiStyle.css'
import "animate.css/animate.min.css"
import "animate.css/animate.compat.css"
import VueLazyLoad from "vue-lazyload";
import './plugins/element.js'

Vue.config.productionTip = false

Vue.use(VueLazyLoad,{
  preLoad: 1.3,
  error: 'https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Gif/cat_loading.gif',   //请求失败后显示的图片
  loading: 'https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Gif/Squidward.gif',   //加载的loading过渡图片
  attempt: 2     // 加载图片数量
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');
