<template>
    <div class="photo" :style="articleIllustration">
        <transition name="articleScroll" appear>
            <div style="font-weight:700 " class="yuki-scroll-position selectNG" :class="[{'yuki-scroll-disappear':menuStatus || !scrollChange}]">
                <span id="index-scroll">scroll</span><br>
                <div id="yuki-arrow" style=""><span class="arrowUp"></span></div>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "ArticlePhoto",
        props:['photoUrl','menuStatus','scrollChange'],
        data: function () {
            return {
                articleIllustration: {
                    backgroundImage: "url(" + this.photoUrl + ")",
                },
            }
        },
    }
</script>

<style scoped>
    .yuki-scroll-disappear{
        opacity: 0;
    }
    .yuki-scroll-position {
        position: absolute;
        bottom: 23px;
        left: 80px;
        /*width:auto;*/
        text-align: center;
        z-index: 10;
        transition: 0.5s ease-in-out;
    }

    #index-scroll {
        color: #e6b6ff;
        text-shadow: 4px 4px 10px rgb(128, 255, 196);
        font-family: sans-serif;
        letter-spacing: 1px;
        font-size: 13px;
    }

    .arrowUp:after {
        content: '';
        display: inline-block;
        width: 8px;
        height: 8px;
        border-top: 1px solid #e6b6ff;
        border-right: 1px solid #e6b6ff;
        transform: rotate(135deg); /*旋转角度*/
        -webkit-transform: rotate(135deg);
    }

    #yuki-arrow {
        animation: yukiArrow 1.4s cubic-bezier(0.215, 0.61, 0.355, 1) 0s infinite alternate;
        margin-left: 0;
        margin-right: 0;
        margin-top: 6px;
    }

    @media only screen and ( max-width: 769px) {
        .yuki-scroll-position{
            display: none;
        }
    }

    .articleScroll-enter,
    .articleScroll-leave-to {
        opacity: 0;
    }

    .articleScroll-enter-active,
    .articleScroll-leave-active {
        transition: all 1.5s ease-in-out;
        transition-delay: 2.7s;

    }
</style>
<style scoped src="../../assets/css/article/yuki-article.css"></style>