<template>
    <div v-if="readyState">
        <Nav :menu-status="menuStatus" @MenuStateChange="menuStatusChange"></Nav>
        <IndexCenterPart :scroll-change="scrollChange" :menu-status="menuStatus"></IndexCenterPart>
        <IndexLogoFont></IndexLogoFont>
        <IndexIllustrationInfo :scroll-change="scrollChange" :menu-status="menuStatus"></IndexIllustrationInfo>
        <IndexTiledBackground></IndexTiledBackground>
        <IndexTopBackground :bg-url="topBGUrl" :menu-status="menuStatus"></IndexTopBackground>
        <IndexScroll :scroll-change="scrollChange" :menu-status="menuStatus"></IndexScroll>
        <IndexArticlePart :parallax-state="parallaxState" :menu-status="menuStatus" :article-list="articleList"></IndexArticlePart>
        <transition name="indexFooter" appear>
            <IndexEnd :is-over="pageNumber===pageCount+1" @nextPage="nextPage"></IndexEnd>
        </transition>
        <transition name="indexFooter" appear>
            <Footer :no-line="false"></Footer>
        </transition>
        <div class="selectNG yuki-cursor-arrow" :class="[parallaxState?'parallax-alter':'parallax',{'parallax-leave':menuStatus || scrollChange}]" @click="changeParallaxState">Parallax</div>
        <div class="flandre-position" @click="returnTop" :class="[{'flandre-disappear':menuStatus || scrollChange}]">
            <img @mousedown.prevent="()=>{return false;}" class="flandre-img"
                 src="https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Illustration/flandre.png">
        </div>
    </div>
</template>

<script>
    import IndexTiledBackground from "../components/index/IndexTiledBackground";
    import IndexTopBackground from "../components/index/IndexTopBackground";
    import IndexIllustrationInfo from "../components/index/IndexIllustrationInfo";
    import IndexCenterPart from "../components/index/IndexCenterPart";
    import IndexLogoFont from "../components/index/IndexLogoFont";
    import IndexScroll from "../components/index/IndexScroll";
    import IndexEnd from "../components/index/IndexEnd";
    import IndexArticlePart from "../components/index/IndexArticlePart";
    import Footer from "../components/Footer";
    import Nav from "../components/Nav";

    export default {
        name: "Index",
        components: {
            Nav,
            Footer,
            IndexArticlePart,
            IndexEnd,
            IndexScroll,
            IndexLogoFont, IndexCenterPart, IndexIllustrationInfo, IndexTopBackground, IndexTiledBackground
        },
        data: function () {
            return {
                parallaxState:false,
                pageCount: 1,
                pageNumber: 1,
                switchKey: false,
                readyState: false,
                hasError: false,
                scrollChange: true,
                menuStatus: false,
                topBGUrl: 'https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Illustration/61246696_p1.png',
                articleList: []
            }
        },
        methods: {
            changeParallaxState(){
                this.parallaxState=!this.parallaxState;
            },
            menuStatusChange() {
                if (this.switchKey) {
                    this.menuStatus = !this.menuStatus;
                    this.$emit('menuStateChange');
                    this.switchKey = false;
                    if (this.menuStatus === false) {
                        setTimeout(() => {
                            this.switchKey = true
                        }, 600);
                    } else {
                        setTimeout(() => {
                            this.switchKey = true
                        }, 800);
                    }
                }
            },
            scrollJudge() {
                var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
                this.scrollChange = scrollTop < window.screen.availHeight * 0.3;
            },
            returnTop() {
                window.scroll({top:0,left:0,behavior:'smooth'});
            },
            nextPage() {
                if (this.pageNumber <= this.pageCount) {
                    axios.get('http://47.106.13.19:8848/index/' + this.pageNumber).then((res) => {
                        this.pageCount = res.data.pageCount;
                        this.articleList = this.articleList.concat(res.data.articleList);
                        this.pageNumber += 1;
                    })
                }
            },
            illustrationLoad(key) {
                let newImg = new Image();
                let f = () => {
                    newImg.src = this.topBGUrl;
                    newImg.onload = () => {
                        this.$emit('hasLoaded');
                        this.readyState = true;
                    };
                    //加载错误时的情况
                    newImg.onerror = () => {
                        this.hasError = true;
                        this.$emit('Error');
                    };
                };
                if (key)
                    setTimeout(() => f(), 2100);
                else
                    f();
            }
        },
        created() {
            window.addEventListener('scroll', () => this.scrollJudge());
            this.nextPage();
            document.title='YUKIBAKO | いつか人は一人になって';
        },
        mounted() {
            if (!this.$store.state.hadEntered)
                window.onload = () => {
                    this.illustrationLoad(false);
                    this.$store.state.hadEntered = true;
                };
            else
                this.illustrationLoad(true);
            setTimeout(() => {
                this.switchKey = true
            }, 4500);
        },
        destroyed() {
            this.$emit('viewChange');
        }
    }
</script>

<style scoped>
    .parallax-leave{
        opacity: 0;
    }

    .parallax,
    .parallax-alter{
        position: fixed;
        bottom: 0;
        right: 100px;
        font-size: 20px;
        letter-spacing: 3px;
        line-height: 35px;
        font-family:"Bradley Hand ITC" ;
        transition: 0.35s ease-in-out;
    }
    .parallax{
        color: #c186ff;
        text-shadow: 4px 4px 10px rgb(118, 255, 110);
    }

    .parallax:hover,
    .parallax-alter{
        color: #ff681d;
        text-shadow: 4px 4px 10px rgb(255, 244, 31);
    }

    .menuOpen {
        filter: blur(2px);
    }


    .index-flandre-enter {
        transform: translateX(71px);
    }

    .index-flandre-leave-to {
        opacity: 0;
    }

    .index-flandre-enter-active,
    .index-flandre-leave-active {
        transition: all 0.6s ease-out;
    }

    .indexFooter-enter,
    .indexFooter-leave-to {
        opacity: 0;
    }

    .indexFooter-enter-active,
    .indexFooter-leave-active {
        transition: 2s ease-in-out;
        transition-delay: 0.6s;
    }
    @media only screen and ( max-width: 1180px){
        .parallax{
            display: none;
        }
    }
</style>