<template>
    <div id="app">
        <div class="bg yuki-cursor-wait"></div>
        <Loading :is-load="isLoad"></Loading>
        <transition name="routerView" appear>
            <router-view @hasLoaded="isLoad=false"
                         @Error="error=true"
                         @viewChange="routerViewChange"></router-view>
        </transition>
    </div>
</template>

<script>
    import Loading from "./views/Loading";

    export default {
        components: {Loading},
        data: function () {
            return {
                isLoad: true,
                error: false,
                test: 0,
            }
        },
        methods: {
            routerViewChange() {
                setTimeout(() => {
                    this.isLoad = true
                }, 750);
            },
        },
    }
</script>

<style>
    .bg {
        position: fixed;
        width: 100vw;
        height: 100vh;
        top: 0;
        left: 0;
        background: #fff;
        z-index: -99;
    }


    /*================================*/
    .routerView-leave-to {
        opacity: 0;
    }

    .routerView-leave-active {
        transition: 0.9s ease-in-out;
    }
</style>
