<template>
    <div>
        <transition name="bgMenu">
            <div v-show="menuStatus" class="yuki-nav-bg"></div>
        </transition>
        <transition name="designMenu">
            <!--右下角设计签名-->
            <div v-show="menuStatus" class="yuki-nav-index-design selectNG">
                <span class="yuki-nav-index-design-font">Designed by yukibako</span>
            </div>
        </transition>
        <div class="yuki-homepage-button selectNG">
            <!--菜单控制-->
            <transition name="menu" appear>
                <div class="yuki-button-size yuki-cursor-arrow" id="yuki-menu-button" @click="$emit('MenuStateChange')">
                    <img @mousedown.prevent="()=>{return false;}" class="yuki-button-img"
                         src="https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Various/Menu3.png">
                    <span class="yuki-button-font" id="yuki-menu-font">メニュー</span>
                </div>
            </transition>
            <!-- 音乐控制-->
            <transition name="music" appear>
                <div class="yuki-button-size yuki-cursor-arrow" @click="$emit('MusicStateChange')">
                    <img @mousedown.prevent="()=>{return false;}" class="yuki-button-img" src="https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Various/marisha3.png">
                    <span class="yuki-button-font" id="yuki-music-font">ミュージック</span>
                </div>
            </transition>
        </div>
        <transition name="navPanel">
            <div v-show="menuStatus" class="yuki-nav-menu-panel">
                <!--=====背景=====-->
                <div class="yuki-nav-index-bg" style="background: rgba(0,0,0,0.13)"></div>
                <BgInMenuPanel></BgInMenuPanel>

                <transition name="marishaMenu">
                    <!--====魔理沙===-->
                    <Marisha v-show="menuStatus"></Marisha>
                </transition>

                <transition enter-active-class="animated animate__bounceInDown"
                            leave-active-class="animated animate__bounceOutUp" :duration="{enter:800 , leave:500}">
                    <!--=====比那名居=====-->
                    <Tenshi v-show="menuStatus"></Tenshi>
                </transition>

                <!--======索引=========-->
                <Index></Index>

                <!--=====头像======-->
                <Avatar></Avatar>

                <!--=====用户信息===-->
                <UserInfo></UserInfo>

                <!--=====欢迎语======-->
                <Welcome></Welcome>


                <transition name="yukikazeMenu">
                    <!--=====yukikaze========-->
                    <Yukikaze v-show="menuStatus"></Yukikaze>
                </transition>


            </div>
        </transition>
        <transition name="hamburger" appear>
            <div class="yuki-hamburger-position" @click="$emit('MenuStateChange')">
                <div class="hamburger" id="hamburger-1" :class="[{'is-active':menuStatus}]">
                    <span class="line"></span>
                    <span class="line"></span>
                    <span class="line"></span>
                </div>
                <span id="menu" :class="[{'noneDisplay':menuStatus}]">Menu</span>
            </div>
        </transition>
        <NavIIPanelInIndex :nav-i-i-state="menuStatus"></NavIIPanelInIndex>
        <transition name="navIIBg">
            <div class="Nav-bg" v-show="menuStatus">
                <div class="yuki-navII-menu-welcome selectNG">
                    <span class="yuki-navII-menu-welcome-font">ようこそ</span><br>
                    <span class="yuki-navII-menu-welcome-font">ユキバコのブログへ</span><br>
                    <span class="yuki-navII-menu-welcome-font">どうぞご自由にご覧ください...</span>
                </div>
                <transition name="navIIYukikaze">
                    <!--=====yukikaze========-->
                    <div  v-show="menuStatus" class="yuki-navII-welcome-yukikaze selectNG">
                        <img @mousedown.prevent="()=>{return false;}" src="https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Various/yukikaze_1.png" class="yuki-button-img">
                    </div>
                </transition>
            </div>
        </transition>
    </div>
</template>

<script>
    import BgInMenuPanel from "./nav/BgInMenuPanel";
    import Avatar from "./nav/Avatar";
    import UserInfo from "./nav/UserInfo";
    import Welcome from "./nav/Welcome";
    import Yukikaze from "./nav/Yukikaze";
    import Tenshi from "./nav/Tenshi";
    import Marisha from "./nav/Marisha";
    import Index from "./nav/Index";
    import NavPanel from "./navII/NavPanel";
    import NavIIPanelInIndex from "./nav/NavIIPanelInIndex";

    export default {
        name: "Nav",
        components: {
            NavIIPanelInIndex,
            NavPanel,
            Marisha,
            Tenshi,
            Yukikaze,
            Welcome,
            UserInfo,
            Index,
            Avatar,
            BgInMenuPanel
        },
        props: ['menuStatus'],

    }
</script>

<style scoped>
    .yuki-nav-bg {
        position: fixed;
        width: 100vw;
        height: 100vh;
        z-index: 50;
        background: rgba(0, 0, 0, 0.5);
        transition: 0.4s opacity ease-in-out;
    }

    .yuki-homepage-button {
        position: fixed;
        top: 1.7em;
        right: 1.5em;
        z-index: 99;
        /*text-align: center;*/
    }

    /*按钮大小*/
    .yuki-button-size {
        box-sizing: content-box;
        height: 63px;
        text-align: center;
        z-index: 99;
    }

    /*图片设置*/
    .yuki-button-img {
        display: block;
        height: 100%;
        margin: 0 auto;
    }

    /*文字描述设置*/
    .yuki-button-font {
        color: #ff999b;
        text-shadow: 4px 3px 8px #66ccff;
        font-size: 13px;
        font-weight: 500;
        display: block;
        letter-spacing: -1px;
    }

    #yuki-menu-font {
        line-height: 15px;
    }

    #yuki-music-font {
        padding-left: 17px;
        color: #88d583;
    }

    /*菜单*/
    #yuki-menu-button {
        margin-bottom: 2.5em;
    }

    /*=====================================*/
    #menu {
        font-size: 13px;
        transition: 0.3s linear;
        font-weight: 200;
        font-family: SansSerif;
        letter-spacing: 1px;
        color: #5555d5;
        display: block;
        transform: translateY(-4px);
    }

    /*=====================================*/
    .hamburger-enter,
    .hamburger-leave-to {
        opacity: 0;
        transform: scale(0.3);
    }

    .hamburger-enter-active,
    .hamburger-leave-active {
        transition: 0.65s ease-in-out;
        transition-delay: 2.5s;
    }
</style>
<style scoped src="../assets/css/nav/yukibako_menu.css"></style>
<style scoped src="../assets/css/nav/yiki-navI-animation.css"></style>
<style scoped src="../assets/css/navII/yuki-Nav-button.css"></style>
<style scoped>
    .yuki-hamburger-position {
        display: none;
        z-index: 99;
        text-align: center;
        font-family: sans-serif;
        user-select: none;
        -moz-user-select: none;
        -webkit-user-select: none;
        position: fixed;
        top: 45px;
        right: 40px;
    }

    .noneDisplay {
        opacity: 0;
    }

    .is-active .line {
        background: #66ccff;
    }

    .Nav-bg {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background: rgba(255, 255, 255, 0.3);
        z-index: 80;
        display: none;
    }

    .yuki-navII-welcome-yukikaze {
        position: fixed;
        right: 30px;
        bottom: 0;
        height: 150px;
        opacity: 0.5;
    }

    .yuki-navII-menu-welcome {
        position: fixed;
        right: 170px;
        bottom: 4%;
        color: #b8a5c5;
        line-height: 50px;
    }

    .yuki-navII-menu-welcome-font {
        font-size: 25px;
        font-family: DFM;
        text-shadow: 4px 4px 10px rgb(113, 255, 238);
    }

    @media only screen and (min-width: 1200px) and (max-width: 1299px) {
        .yuki-nav-menu-panel {
            width: 990px;
        }
    }

    @media only screen and (max-width: 1200px) {
        .yuki-nav-menu-panel,
        .yuki-nav-index-design,
        .yuki-homepage-button,
        .yuki-nav-bg {
            display: none;
        }

        .yuki-hamburger-position,
        .Nav-bg {
            display: block;
        }
    }

    @media only screen and (max-width: 768px) {
        .yuki-navII-menu-welcome {
            display: none;
        }
        .yuki-hamburger-position{
            top: 30px;
            right: 25px;
        }

        .yuki-navII-welcome-yukikaze {
            display: none;
        }
    }
</style>
<style scoped src="../assets/css/navII/yuki-navII-animation.css"></style>
<style scoped src="../assets/css/Index/yuki-index-animation.css"></style>



