<template>
    <transition name="tiledBg" appear>
        <div class="tiledBackground" :style="bgUrl"></div>
    </transition>
</template>

<script>
    export default {
        name: "ArticleTiledBackground",
        data: function () {
            return {
                bgUrl: {
                    backgroundImage: "url( https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Bg/sakura.png )",
                }
            }
        }
    }
</script>

<style scoped>
    .tiledBackground {
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        width: 100vw;
        z-index: -10;
        opacity: 0.6;
    }

    .tiledBg-enter,
    .tiledBg-leave-to {
        opacity: 0;
    }

    .tiledBg-enter-active,
    .tiledBg-leave-active {
        transition: all 1.3s ease-in-out;
        transition-delay: 0.6s;
    }
</style>

<style scoped src="../../assets/css/yuki-animation.css"></style>

