<template>
    <div :id="articleId" class="yuki-blog-list-section row"
         :class="[{'yuki-blog-content-right':isRight},{'yuki-blog-content-left':!isRight},{'menuChange':menuStatus}]">
        <div class="col-md-9 yuki-blog-content-area">
            <transition :name="[isRight?'leftImgChange':'rightImgChange']" appear>
                <div v-if="isShow" class="yuki-blog-img">
                    <div class="test">
                        <div class="ttw" v-lazy:background-image="illustrationUrl"></div>
                    </div>
                </div>
            </transition>
            <transition :name="[isRight?'rightContentChange':'leftContentChange']" appear>
                <div v-if="isShow" @click="goToArticleWithId" class="yuki-blog-content" :style="cardBgUrl">
                    <div class="yuki-card-bg"></div>
                    <h4 class="yuki-blog-title">{{title}}</h4>
                    <div class="yuki-meta">
                        <div class="date">
                            <p>{{getDay(date)}}<sup>{{dayChange(getDay(date))}}</sup>{{monthChange(getMonth(date))}}
                                {{getYear(date)}}
                            </p>
                        </div>
                        <div class="author">
                            <p>By {{author}}</p>
                        </div>
                        <div class="type">
                            <p>{{type.name}}</p>
                        </div>
                    </div>
                    <div>
                        <p class="blog-decisions" style="color: #afafaf;">{{introduction}}...</p>
                        <div style="position: relative;top: 4px">
                            <span style="color: pink;font-size:14px;padding-top: 10px">Click To Read More</span>
                        </div>
                    </div>
                </div>
            </transition>
        </div>
    </div>
</template>

<script>
    export default {
        name: "IndexArticleCard",
        props: [
            'title',
            'author',
            'type',
            'illustrationUrl',
            'introduction',
            'isRight',
            'date',
            'menuStatus',
            'articleId',
            'isParallax'
        ],
        data: function () {
            return {
                isShow: false,
                cardBgUrl: {
                    backgroundImage: "url( https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Bg/bg.jpg )",
                },
            }
        },
        methods: {
            getYear(date) {
                return date.slice(0, 4);
            },
            getMonth(date) {
                return date.slice(5, 7);
            },
            getDay(date) {
                return date.slice(8, 10);
            },
            monthChange(month) {
                switch (month) {
                    case '01':
                        return "Jan";
                    case '02':
                        return "Feb";
                    case '03':
                        return "Mar";
                    case '04':
                        return "Apr";
                    case '05':
                        return "May";
                    case '06':
                        return "Jun";
                    case '07':
                        return "Jul";
                    case '08':
                        return "Aug";
                    case '09':
                        return "Sept";
                    case '10':
                        return "Oct";
                    case '11':
                        return "Nov";
                    case '12':
                        return "Dec";
                }
            },
            dayChange(day_string) {
                let day = parseInt(day_string);
                if (day % 10 == 1)
                    return 'st';
                else if (day % 10 == 2)
                    return 'nd';
                else if (day % 10 == 3)
                    return 'rd';
                else
                    return 'th';
            },
            displayJudge() {
                setTimeout(() => {
                    var elementById = document.getElementById(this.articleId + '');
                    if (this.$route.path === '/' && !this.isShow) {
                        let rect = elementById.getBoundingClientRect();
                        let viewWindow = window.innerHeight || document.documentElement.clientHeight;
                        // let isIntoView = (rect.top >= 0) && (rect.bottom <= viewWindow);
                        let isIntoView = rect.top <= viewWindow;
                        if (isIntoView === true) this.isShow = true;
                    }
                    // let rect = document.getElementById(this.articleId+'').getBoundingClientRect();
                }, 80)
            },
            goToArticleWithId() {
                this.$store.state.articleId = this.articleId;
                this.$router.replace('/article')
            }
        },
        mounted() {
            // console.log(this.$route.path==='/')
            this.displayJudge();
            window.addEventListener('scroll', () => this.displayJudge());
            //<!-- Parallax -->
            window.addEventListener('mousemove', (e) => {
                    let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
                    document.querySelectorAll('.yuki-blog-img').forEach(Img => {
                        if (this.isParallax) {
                            let x = (e.pageX - window.innerWidth  / 2) * 4 / 550;
                            let y = ((e.pageY-scrollTop) - window.innerHeight / 2) * 4 / 450;
                            Img.style.transform = "translate(" + x + "px," + y + "px)";
                        }
                    });
                    document.querySelectorAll('.yuki-blog-content').forEach(Img => {
                        if (this.isParallax) {
                            let x = (e.pageX - window.innerWidth / 2) * 10 / 500;
                            let y = ((e.pageY-scrollTop) - window.innerHeight / 2) * 10 / 400;
                            Img.style.transform = "translate(" + x + "px,calc(-50% + " + y + "px))";
                        }
                    })
                }
            )
            ;
        }
    }
</script>

<style scoped>
    .leftImgChange-enter,
    .leftImgChange-leave-to,
    .rightImgChange-enter,
    .rightImgChange-leave-to,
    .leftContentChange-enter,
    .leftContentChange-leave-to,
    .rightContentChange-enter,
    .rightContentChange-leave-to {
        opacity: 0;
    }

    .leftImgChange-enter-active,
    .leftImgChange-leave-active,
    .rightImgChange-enter-active,
    .rightImgChange-leave-active,
    .leftContentChange-enter-active,
    .leftContentChange-leave-active,
    .rightContentChange-enter-active,
    .rightContentChange-leave-active {
        transition: 0.8s ease-in-out;
    }


    .menuChange {
        filter: blur(2px);
        webkit-transform: translateZ(0);
        -moz-transform: translateZ(0);
        -ms-transform: translateZ(0);
        -o-transform: translateZ(0);
        transform: translateZ(0);
    }

    .test {
        overflow: hidden;
        box-shadow: 0 5px 10px 5px rgba(110, 110, 110, .4);
    }

    .ttw {
        /*上面这个是背景不要重复*/
        background-repeat: no-repeat;
        /*让图片随屏幕大小同步缩放*/
        background-size: cover;
        background-position: center 35%;
        transition: 0.45s ease-out;
        height: 100%;
        width: 100%;
    }

    .ttw:hover {
        transform: scale(1.05);
    }

    .yuki-card-bg {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: 50;
        transition: background-color 0.4s ease-out;
    }

    .yuki-card-bg:hover {
        background: rgba(102, 102, 255, 0.06);
    }

    @media only screen and (min-width: 992px) {
        .leftImgChange-enter,
        .leftImgChange-leave-to {
            transform: translateX(-100px);
        }

        .rightImgChange-enter,
        .rightImgChange-leave-to {
            transform: translateX(100px);
        }

        .leftContentChange-enter,
        .leftContentChange-leave-to {
            transform: translate(-100px, -50%) !important;
        }

        .rightContentChange-enter,
        .rightContentChange-leave-to {
            transform: translate(100px, -50%) !important;
        }


        .test {
            width: 550px;
            height: 385px;
            /*background: pink;*/
            transition: 0.6s;
        }
    }

    @media only screen and (max-width: 992px) {
        .test {
            /*width: 90vw;*/
            /*max-width: 720px;*/
            width: 100%;
            height: 51vw;
            max-height: 405px;
            /*background: #66ccff;*/
        }
    }


    .yuki-blog-list-section {
        position: relative;
        margin-bottom: 90px;
        transition: 0.5s ease-in-out;
    }

    .yuki-blog-content-left .yuki-blog-content-area,
    .yuki-blog-content-left .yuki-blog-img {
        float: right;
    }

    .yuki-blog-content-left .yuki-blog-content {
        right: auto;
        left: -200px;
    }

    .yuki-blog-content {
        position: absolute;
        right: -200px;
        top: 50%;
        max-width: 460px;
        padding: 30px;
        border: 8px solid #fff;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
    }

    /*================================卡片内容================================*/
    .yuki-blog-title {
        color: #83c1eb;
        letter-spacing: 1px;
        margin-top: 0;
        font-family: sans-serif;
        transition: 0.5s;
        line-height: 30px;
        text-transform: uppercase;
        padding-bottom: 20px;
        font-weight: 500;
    }

    .yuki-meta {
        padding-bottom: 22px;
    }

    .yuki-meta p {
        font-size: 14px;
        margin: 0;
        line-height: 1;
        color: #afafaf;
    }

    .yuki-meta div {
        padding-right: 10px;
        margin-right: 7px;
        border-right: 1px solid #afafaf;
        display: inline-block;
    }

    .yuki-meta div:last-child {
        padding-right: 0;
        margin-right: 0;
        border: 0;
    }

    .yuki-meta p {
        font-size: 14px;
        margin: 0;
        line-height: 1;
        color: #afafaf;
    }

    .yuki-meta div {
        padding-right: 10px;
        margin-right: 7px;
        border-right: 1px solid #afafaf;
        display: inline-block;
    }

    .yuki-meta div:last-child {
        padding-right: 0;
        margin-right: 0;
        border: 0;
    }
</style>
<style scoped src="../../assets/css/Index/yuki-index.css"></style>
