<template>
    <div class="message-area">
        <div class="message-title selectNG">言いたいことがきっとあるでしょう...0v0</div>
        <div :class="[{'notShow':addressee==null}]" class="addressee selectNG" @click.stop="$emit('receiverClear')"
             v-text="addresseeAlternation"></div>
        <form autocomplete="off" @submit.prevent="commentSubmit">
            <textarea class="yuki-textarea" @hover.prevent="()=>{return false;}" placeholder="こちらに入力してください..." v-model="content" required></textarea>
            <div class="yuki-textarea-lower">
                <div class="yuki-input"><input class="yuki-input-a" style="letter-spacing: 2px;" type="text"
                                               v-model="nickname"
                                               maxlength="10" required placeholder="*お名前は...?"></div>
                <div class="yuki-input"><input class="yuki-input-a" style="letter-spacing: 2px;" type="email"
                                               v-model="email"
                                               placeholder="おメールは...?"></div>
                <div class="yuki-input"><input class="yuki-input-a"
                                               style="color: #fabcc8" type="submit" value="提出する">
                </div>
            </div>
        </form>
    </div>
</template>

<script>
    export default {
        name: "TextArea",
        props: ['superiorId', 'addressee', 'fatherId', 'isFather'],
        data: function () {
            return {
                content: '',
                receiver: '',
                nickname: '',
                email: null,
            }
        },
        methods: {
            commentSubmit() {
                let superiorIdAlternation = this.superiorId;
                let addresseeAlternation = this.addressee;
                if (this.fatherId !== 0) superiorIdAlternation = 0;
                if (this.isFather === true) addresseeAlternation = null;
                axios.post('http://47.106.13.19:8848/comment', {
                    nickname: this.nickname,
                    content: this.content,
                    fatherId: this.fatherId,
                    superiorId: superiorIdAlternation,
                    addressee: addresseeAlternation,
                }).then((ret) => {
                    if (ret.data.code === 201 || ret.data.code===202) {
                        this.$message({
                            message: '届けました!!',
                            type: 'success'
                        });
                        this.$emit('commentUpdate', ret.data.data,ret.data.code);
                    } else
                        this.$message.error(ret.data.message);
                });
            }
        },
        computed: {
            addresseeAlternation: function () {
                if (this.addressee != null)
                    return '@' + this.addressee;
                else return `bye~`;
            }
        }
    }
</script>

<style scoped>
    .notShow {
        opacity: 0;
    }

    .message-area {
        width: 78%;
        margin: 0 auto;
        text-align: center;
        padding-top: 50px;
    }

    .message-title {
        margin: 30px auto 24px auto;
        font-size: 25px;
        color: #e8c3ff;
        text-shadow: 4px 4px 10px rgb(155, 230, 255);
        letter-spacing: 3px;
        text-align: left;
        /*padding-bottom: 20px*/
    }

    .addressee {
        font-size: 15px;
        letter-spacing: 1px;
        color: #f7bac5;
        text-shadow: 4px 4px 10px rgb(255, 253, 160);
        padding-bottom: 5px;
        text-align: left;
        width: 150px;
        position: relative;
        left: 7%;
        transition: 0.4s ease-in-out;
    }

    .addressee:hover {
        color: #80d9ff;
        text-shadow: 4px 4px 12px rgb(254, 255, 162);
        letter-spacing: 2px;
    }

    .yuki-textarea {
        width: 80%;
        max-width: 80%;
        min-width: 79.9%;
        min-height: 150px;
        height: 220px;
        max-height: 300px;
        background: rgba(255, 255, 255, 0.35);
        border: solid #a2f8f5 2px;
        border-radius: 5px;
        box-sizing: border-box;
        padding: 15px 25px 15px 25px;
        font-size: 16px;
        letter-spacing: 2px;
        color: #74e1b2;
        text-shadow: 3px 3px 10px rgb(255, 248, 151);
        background: rgba(255, 255, 255, 0.35) url("https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Various/flandreText.png");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: right;
    }

    .yuki-textarea:focus {
        border-color: #e8c3ff;
        transition: 0.3s ease-in-out;
    }

    .yuki-textarea-lower {
        padding-top: 30px;
    }

    .yuki-input {
        height: 80px;
        /*background: yellow;*/
        display: inline-block;
        width: 28%;
        vertical-align: middle;
    }

    .yuki-input-a {
        background: rgba(255, 255, 255, 0.35);
        width: 80%;
        height: 80%;
        color: #6666ff;
        border: solid 2px rgb(255, 221, 176);
        text-align: center;
        font-size: 15px;
        font-weight: 400;
        border-radius: 8px;
        box-sizing: border-box;
    }

    .yuki-input-a:focus,
    .yuki-textarea:focus {
        outline: none;
    }

    input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
        color: #74e1b2;
        text-shadow: 3px 3px 10px rgb(255, 248, 151);
        font-size: 15px;
        opacity: 1;
    }

    input:-moz-placeholder, textarea:-moz-placeholder {
        color: #74e1b2;
        text-shadow: 3px 3px 10px rgb(255, 248, 151);
        font-size: 15px;
        opacity: 1;
    }

    input::-moz-placeholder, textarea::-moz-placeholder {
        color: #74e1b2;
        text-shadow: 3px 3px 10px rgb(255, 248, 151);
        font-size: 15px;
        opacity: 1;
    }

    input:-ms-input-placeholder, textarea:-ms-input-placeholder {
        color: #74e1b2;
        text-shadow: 3px 3px 10px rgb(255, 248, 151);
        font-size: 15px;
        opacity: 1;
    }

    @media only screen and ( max-width: 768px) {
        .yuki-input {
            display: block;
            margin: 20px 0;
            width: 100%;
        }

        .message-title {
            padding-bottom: 0;
        }

        .yuki-textarea {
            width: 100%;
            max-width: 100%;
            min-width: 99.9%;
            min-height: 200px;
            height: 240px;
        }

        .message-area {
            padding-top: 10px;
        }

        .yuki-input-a {
            width: 85%;
        }

        .yuki-textarea-lower {
            padding-top: 15px;
        }
    }
</style>