<template>
    <transition name="loading" appear>
        <div v-show="readyState && isLoad" class="center selectNG yuki-cursor-wait">
            <transition name="buka" appear>
                <img @mousedown.prevent="()=>{return false;}" v-show="readyState && isLoad" class="buka" :src="ImgUrl">
            </transition>
            <transition name="loadingFont" appear>
                <div v-show="readyState && isLoad" class="loading-font">Loading...</div>
            </transition>
        </div>
    </transition>
</template>

<script>
    export default {
        name: "Loading",
        props: ['isLoad'],
        data: function () {
            return {
                readyState: false,
                ImgUrl: 'https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Gif/buka.gif',
            }
        },
        mounted() {
            let newImg = new Image();
            newImg.src = this.ImgUrl;
            newImg.onload = () => {
                this.readyState = true;
            };
            //加载错误时的情况
            newImg.onerror = () => {
                this.hasError = true;
            }
        }

    }
</script>

<style scoped>
    .center {
        position: fixed;
        top: 190px;
        left: 50vw;
        transform: translateX(-50%);
        height: 290px;
        width: 290px;
        z-index: -95;
    }

    .buka {
        display: block;
        margin: 0 auto;
        max-height: 100%;
    }

    .loading-font {
        padding-top: 15px;
        text-align: center;
        font-size: 28px;
        color: #c1f0ff;
        font-weight: 600;
        text-shadow: 2px 2px 15px #ffe3f3;
        letter-spacing: 3px;
        font-family: "Lucida Handwriting";
    }

    .loading-enter,
    .loading-leave-to {
        opacity: 0;
    }

    .loadingFont-enter,
    .loadingFont-leave-to {
        transform: translateX(100px);
    }

    .buka-enter,
    .buka-leave-to{
        transform: translateX(-100px);
    }

    .loading-enter-active,
    .loading-leave-active,
    .loadingFont-enter-active,
    .loadingFont-leave-active,
    .buka-enter-active,
    .buka-leave-active{
        transition: 0.6s ease-in-out;
    }

    @media only screen and (min-height: 780px) {
        .center {
            top: 280px;
        }
    }
</style>