<template>
    <div>
        <NavIIStripe :nav-i-i-state="navIIState" @navStateChange="navIIStateChagne"></NavIIStripe>
        <NavPanel :nav-i-i-state="navIIState"></NavPanel>
        <NavIIBg :nav-i-i-state="navIIState"></NavIIBg>
    </div>
</template>

<script>
    import NavIIStripe from "./navII/NavIIStripe";
    import NavPanel from "./navII/NavPanel";
    import NavIIBg from "./navII/NavIIBg";

    export default {
        name: "NavII",
        components: {NavIIBg, NavPanel, NavIIStripe},
        data: function () {
            return {
                navIIState: false,
                switchKey: false,
            }
        },
        methods: {
            navIIStateChagne() {
                if (this.switchKey) {
                    this.navIIState = !this.navIIState;
                    this.$emit('menuStateChange');
                    this.switchKey = false;
                    if (this.navIIState === false) {
                        setTimeout(() => {
                            this.switchKey = true
                        }, 600);
                    } else {
                        setTimeout(() => {
                            this.switchKey = true
                        }, 1350);
                    }
                }
            }
        },
        mounted() {
            setTimeout(() => {
                this.switchKey = true
            }, 3500)
        }
    }
</script>

