<template>
    <div>
        <div class="comment">
            <div class="comment-title selectNG">Comments</div>
            <div v-for="comment in CommentCollection.commentList" :key="comment.commentId">
                <div class="comment-main">
                    <div class="reply">
                        <div @click.stop="$emit('replay',comment.nickname,comment.commentId,true)" class="replyChild selectNG">reply</div>
                    </div>
                    <div class="comment-main-userInfo">
                        <div class="comment-main-userInfo-avatar"
                             :style="'background-image: url(' + avatarAlternation(comment.avatarUrl) +')'"></div>
                        <div class="comment-main-time selectNG">{{comment.nickname}} <br><span style="font-size: 12px" v-text="dateAlternation(comment.date)"></span>
                        </div>
                    </div>
                    <div class="comment-content" v-text="comment.content"></div>
                </div>
                <div v-for="childComment in comment.childComments" :key="childComment.commentId" class="comment-child">
                    <div class="reply">
                        <div @click.stop="$emit('replay',childComment.nickname,comment.commentId,false)" class="replyChild selectNG">reply</div>
                    </div>
                    <div class="comment-main-userInfo">
                        <div class="comment-main-userInfo-avatar"
                             :style="'background-image: url(' + avatarAlternation(childComment.avatarUrl) +')'"></div>
                        <div class="comment-main-time selectNG">{{childComment.nickname}} <br><span style="font-size: 12px" v-text="dateAlternation(childComment.date)"></span>
                        </div>
                    </div>
                    <div class="comment-content-child">
                        <span v-if="childComment.addressee!=null" class="selectNG"
                              style="color: #8cd695;text-shadow: 4px 4px 10px rgb(213,206,255);"
                              v-text="'@'+childComment.addressee+': '"></span><span
                            v-text="childComment.content"></span>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="isNullJudge" class="comment-nothing selectNG">ここには何もないですわ〒_〒...</div>
    </div>
</template>

<script>
    import TextArea from "../Comment/TextArea";

    export default {
        name: "CommentII",
        components: {TextArea},
        props: [
            'CommentCollection',
        ],
        methods: {
            avatarAlternation(url) {
                if (url == null)
                    return this.CommentCollection.defaultAvatar;
                return url;
            },
            dateAlternation(date){
                return date.slice(0,16)
            }
        },
        computed:{
            isNullJudge:function () {
                return this.CommentCollection.commentList.length === 0;
            }
        },
        // mounted() {
        //     console.log('CommentII:');
        //     console.log(this.CommentCollection.commentList);
        //     console.log(this.CommentCollection.commentList===undefined);
        //     console.log(this.CommentCollection.commentList===null)
        //     console.log(this.isNullJudge)
        // }
    }
</script>

<style scoped>

</style>
