<template>
    <div v-if="readyState">
        <NavII @menuStateChange="menuStatus=!menuStatus"></NavII>
        <ArticleTiledBackground></ArticleTiledBackground>
        <div class="article-fixDiv">
            <transition name="articleIllustrationInfo" appear>
                <div class="articleIllustrationPanel selectNG"
                     :class="[{'yuki-Article-Illustration-change':menuStatus ||!scrollChange}]">
                    <a target="_blank"
                       :href="'https://www.pixiv.net/artworks/'+articleData.articleInfo.illustration.illustrationId">
                            <span id="articleIllustrationTitle"
                                  v-text="articleData.articleInfo.illustration.illustrationName"></span>
                    </a><br>
                    <a target="_blank"
                       :href="'https://www.pixiv.net/users/'+articleData.articleInfo.illustration.illustratorId">
                            <span id="articleIllustrationArtist"
                                  v-text="'Illustrator :'+articleData.articleInfo.illustration.illustratorName"></span>
                    </a>
                </div>
            </transition>
        </div>
        <div class="content">
            <transition name="bg" appear>
                <ArticlePhoto :menu-status="menuStatus" :scroll-change="scrollChange"
                              :photo-url="articleData.articleInfo.illustration.illustrationUrl"></ArticlePhoto>
            </transition>
            <div class="title">
                <div class="title-position">
                    <transition name="title" appear>
                        <div>
                            <div style="transition: 0.6s ease-in-out"
                                 :class="[{'yuki-Article-title-change':!scrollChange}]">
                                <span id="title" v-html="articleData.articleInfo.title"></span>
                            </div>
                        </div>
                    </transition>
                </div>
                <transition name="titleInfo" appear>
                    <div class="title-info">
                        <div style="transition: 0.6s ease-in-out"
                             :class="[{'yuki-Article-titleInfo-change':!scrollChange}]">
                            <span v-text="'Author: '"></span><span class="articleAuthor"
                                                                   v-html="articleData.articleInfo.author"></span><br>
                            <span style="margin-right: 5px"
                                  v-text="dateAlternation"></span><span
                                v-text="'  |  '"></span>
                            <span style="margin-left: 5px" v-text="articleData.articleInfo.type.name"></span>
                        </div>
                    </div>
                </transition>
            </div>
            <transition name="normal" appear>
                <div>
                    <div class="line"></div>
                    <ArticleContentBody :article-main-list="articleData.articleContentList"></ArticleContentBody>
                    <CommentII @replay="changeReceiver(arguments)"
                               :comment-collection="articleData.articleComment"></CommentII>
                    <TextArea :father-id="commentFatherId" :is-father="isFromFather"
                              @commentUpdate="updateComment(arguments)" @receiverClear="clearReceiver"
                              :addressee="receiver" :superior-id="articleData.articleInfo.articleId"></TextArea>
                    <Footer :no-line="true"></Footer>
                </div>
            </transition>
        </div>
        <div class="flandre-position" @click="returnTop" :class="[{'flandre-disappear':menuStatus || scrollChange}]">
            <img @mousedown.prevent="()=>{return false;}" class="flandre-img"
                 src="https://cdn.jsdelivr.net/gh/yukibako/ImgHosting/Illustration/flandre.png">
        </div>
    </div>
</template>

<script>
    import NavII from "../components/NavII";
    import ArticleTiledBackground from "../components/Article/ArticleTiledBackground";
    import ArticleComment from "../components/Article/ArticleComment";
    import Footer from "../components/Footer";
    import ArticleContent from "../components/Article/ArticleContent";
    import ArticleContentBody from "../components/Article/ArticleContentBody";
    import ArticlePhoto from "../components/Article/ArticlePhoto";
    import TextArea from "../components/Comment/TextArea";
    import CommentII from "../components/CommentII/CommentII";

    export default {
        name: "Article",
        components: {
            CommentII,
            TextArea,
            ArticlePhoto,
            ArticleContentBody, ArticleContent, Footer, ArticleComment, ArticleTiledBackground, NavII
        },
        data: function () {
            return {
                readyState: false,
                hasError: false,
                scrollChange: true,
                menuStatus: false,
                articleData: {},
                //评论相关变量
                isFromFather: null,
                receiver: null,
                commentFatherId: 0,
            }
        },
        methods: {
            scrollJudge() {
                let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
                this.scrollChange = scrollTop < window.screen.availHeight * 0.3;
            },
            returnTop() {
                window.scroll({top: 0, left: 0, behavior: 'smooth'});
            },
            illustrationLoad(key) {
                let newImg = new Image();
                let f = () => {
                    newImg.src = this.articleData.articleInfo.illustration.illustrationUrl;
                    newImg.onload = () => {
                        this.$emit('hasLoaded');
                        this.readyState = true;
                    };
                    //加载错误时的情况
                    newImg.onerror = () => {
                        this.hasError = true;
                        this.$emit('Error');
                    };
                };
                if (key)
                    setTimeout(() => f(), 2100);
                else
                    f();
            },
            changeReceiver(result) {
                let y = document.documentElement.scrollHeight || document.body.scrollHeight;
                let x = document.documentElement.scrollWidth || document.body.scrollWidth;
                this.receiver = result[0];
                this.commentFatherId = result[1];
                this.isFromFather = result[2];
                if (x > 330 && x < 400) {
                    window.scroll({top: y - 1030, left: 0, behavior: 'smooth'});
                } else
                    window.scroll({top: y, left: 0, behavior: 'smooth'});
            },
            clearReceiver() {
                this.receiver = null;
                this.commentFatherId = 0;
            },
            updateComment(result) {
                //评论全换
                if (result[1] == 201) {
                    this.articleData.articleComment.commentList = result[0];
                }
                //只换单个评论
                else {
                    for (let i = 0; i < this.articleData.articleComment.commentList.length; i++) {
                        if (this.articleData.articleComment.commentList[i].commentId === result[0].commentId) {
                            this.articleData.articleComment.commentList[i].childComments = result[0].childComments;
                            break;
                        }
                    }
                }
            }
        },
        computed: {
            dateAlternation: function () {
                return this.articleData.articleInfo.date.slice(0, 10)
            }
        },
        created() {
            axios.get('http://47.106.13.19:8848/article/' + this.$store.state.articleId).then((res) => {
                this.articleData = res.data.data;
                document.title=this.articleData.articleInfo.title+' | YUKIBAKO';
            });
            window.addEventListener('scroll', () => this.scrollJudge());
        },
        mounted() {
            if (!this.$store.state.hadEntered)
                window.onload = () => {
                    this.illustrationLoad(false);
                    this.$store.state.hadEntered = true;
                };
            else
                this.illustrationLoad(true);
        },
        destroyed() {
            this.$emit('viewChange');
        },
    }
</script>

<style scoped>
    .articleAuthor {
        color: #e6b6ff;
        text-shadow: 4px 4px 10px rgb(128, 255, 196);
    }

    .yuki-Article-title-change {
        opacity: 0;
        filter: blur(3px);
        transform: translateY(60px);
    }

    .yuki-Article-titleInfo-change {
        opacity: 0;
        filter: blur(3px);
        transform: translateX(-30px);
    }

    .yuki-Article-Illustration-change {
        opacity: 0;
    }

    /*====================================================*/

    a, a:focus {
        text-decoration: none;
    }

    a:hover {
        text-decoration: none;
    }

    .article-fixDiv {
        position: absolute;
        right: 0;
        top: 0;
        width: 50vw;
        height: 100vh;
        /*background: rgba(102, 204, 255, 0.4);*/
        z-index: 10;
    }

    .articleIllustrationPanel {
        position: absolute;
        bottom: 2vh;
        right: 1.5vw;
        background: rgba(253, 235, 227, 0.6);
        padding: 10px 12px;
        border-radius: 8px;
        line-height: 23px;
        letter-spacing: 2px;
        font-size: 14px;
        text-align: center;
        font-weight: 600;
        box-shadow: 0 0 8px rgba(255, 192, 203, 0.75);
        transition: 0.6s ease-in-out;
    }

    #articleIllustrationTitle {
        letter-spacing: 3px;
        color: #8d65e4;
        /*text-shadow: 4px 4px 15px #ecb025;*/
        text-shadow: 4px 4px 12px rgb(159, 255, 142);
        transition: 0.4s ease-in-out;
    }

    #articleIllustrationArtist {
        letter-spacing: 2px;
        color: #8d65e4;
        text-shadow: 4px 4px 12px rgb(159, 255, 142);
        transition: 0.4s ease-in-out;
        /*text-shadow: 4px 4px 15px #ecb025;*/

    }

    #articleIllustrationTitle:hover,
    #articleIllustrationArtist:hover {
        color: #d67f7f;
    }

    .articleIllustrationInfo-enter,
    .articleIllustrationInfo-leave-to {
        opacity: 0;
    }

    .articleIllustrationInfo-enter-active,
    .articleIllustrationInfo-leave-active {
        transition: 1.2s ease-in-out;
        transition-delay: 2.3s;
    }

    /*==================fixDiv====================*/
    @media only screen and ( max-width: 768px) {
        .article-fixDiv {
            top: 100px;
            width: 100vw;
            height: 56.3vw;
        }
    }

    @media only screen and ( max-width: 500px) {
        .articleIllustrationPanel {
            font-size: 10px;
            padding: 7px 9px;
            line-height: 18px;
        }
    }
</style>
<style scoped src="../assets/css/article/yuki-article.css"></style>
<style scoped src="../assets/css/yuki-animation.css"></style>


