<template>
    <div class="comment-child">
        <div class="comment-main-userInfo">
            <div class="comment-main-userInfo-avatar" :style="avatarUrl"></div>
            <div class="comment-main-time">{{comment.username}} <br><span style="font-size: 12px">{{comment.date.year}}-{{comment.date.month}}-{{comment.date.day}} {{comment.date.hour}}:{{comment.date.minute}}</span></div>
        </div>
        <div class="comment-content-child">
            <span v-if="comment.addressee!=null" style="color: #8cd695;text-shadow: 4px 4px 10px rgb(213,206,255);">@{{comment.addressee}}:&emsp;</span>{{comment.content}}
        </div>
    </div>
</template>

<script>
    export default {
        name: "ChildComment",
        props: [
            'comment',
        ],
        data: function () {
            return {
                avatarUrl: {
                    backgroundImage: "url(" + this.comment.userAvatar + ")",
                },
            }
        }
    }
</script>

